import { getBannerList } from "../../utils/req"

const state = {
    bannerList: []
}
const mutations = {
    changeBannerList(state, data) {
        state.bannerList = data
    }
}
const actions = {
    bannerListActions(context) {
        // console.log(111);
        getBannerList().then(res => {
            // console.log(res);
            context.commit('changeBannerList', res.data.list)
        })
    }

}
const getters = {
    bannerList(state) {
        return state.bannerList
    }
}
export default {
    state,
    mutations,
    actions,
    getters,
    namespaced: true
}


