import {getSeckList} from "../../utils/req";
const state={
    seckList:[ ],
}
const mutations={
    changeSeckList(state,data){
        state.seckList=data
    } ,
}
const actions={
    seckListActions(context){
        getSeckList().then(res=>{
            context.commit('changeSeckList',res.data.list)
        })
    },
}
const getters={
    seckList(state){
        return state.seckList
    },
}
export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


